import { createGlobalStyle } from 'styled-components';
import { theme as mode } from '../../utils/theme';

const { fontColor: fontForLight, background: backgroundLight, sunBlack } = mode.light;
const { fontColor: fontForDark, background: backgroundDark, sunWhite } = mode.dark;
const { linkedInBlack, githubBlack, phoneBlack, mailBlack, locationBlack } = mode.light;
const { linkedInWhite, githubWhite, phoneWhite, mailWhite, locationWhite } = mode.dark;

export const Global = createGlobalStyle`
    body {
        line-height: 1.625;
        background-color: ${props => props.isDark ? backgroundDark : backgroundLight};
    }

    h1, h2, p, div, a {
        color: ${props => props.isDark ? fontForDark : fontForLight};
    }

    button {
        background-image: url("${props => props.isDark ? sunWhite : sunBlack}");
        background-color: transparent;
    }

    .phone {
        background-image: url("${props => props.isDark ? phoneWhite : phoneBlack}");
    }
`;
